html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

body {
  font-family: 'Balsamiq Sans';
  margin: 0; }

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0; }

button {
  padding: 0; }

a {
  text-decoration: none; }

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden; }

body {
  padding-top: 100px;
  background: url("../img/grid.png") repeat; }

.wrapper {
  max-width: 1000px;
  margin: 0 auto; }

.battlefield {
  padding: 100px 0;
  border: 3px solid #03befc;
  border-radius: 50px;
  background-color: #ffffff; }

.battlefield__title {
  font-size: 36px;
  line-height: 36px;
  margin-bottom: 50px;
  text-align: center; }

.fields {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center; }

.fields__playground {
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  position: relative;
  width: 330px;
  height: 330px;
  margin: 0 20px;
  border-collapse: collapse;
  border: 3px solid #03befc;
  background: url("../img/grid.png") repeat; }

.fields__rows-names {
  position: absolute;
  top: 0;
  left: -33px;
  width: 33px;
  height: 330px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column; }

.fields__cols-names {
  position: absolute;
  top: -33px;
  left: 0;
  width: 330px;
  height: 33px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }

.fields__item {
  font-size: 24px;
  line-height: 33px;
  width: 33px;
  height: 33px;
  text-align: center; }
